import React,{Component} from 'react';
// import ReactDOM from 'react-dom';

import styles from './NavBar.module.css'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook } from "@fortawesome/free-brands-svg-icons"
import { faInstagram } from "@fortawesome/free-brands-svg-icons"
import { faGithub } from "@fortawesome/free-brands-svg-icons"
import { faLinkedin } from "@fortawesome/free-brands-svg-icons"

import SelectSearch from 'react-select-search';

import 'bootstrap/dist/css/bootstrap.min.css'; 
import Container from 'react-bootstrap/Container'; 
import Row from 'react-bootstrap/Row'; 
import Col from 'react-bootstrap/Col'; 
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
// import Modal from 'react-bootstrap/Modal'
// import FormControl from 'react-bootstrap/FormControl'; 

import '../Inputrow/courseData.json';

class NavBar extends Component{
    constructor(props){
        super(props)
    }

    render(){
        return(
            <div className={styles.main1Wrapper}>
                <h5>Thank You for Visiting</h5>
                <div className="d-flex" className={styles.contactBar}>
                    <a href="#"><FontAwesomeIcon size='2x' className="mx-2" icon={faFacebook}/></a>
                    <a href="#"><FontAwesomeIcon size='2x' className="mx-2" icon={faInstagram}/></a>
                    <a href="#"><FontAwesomeIcon size='2x' className="mx-2" icon={faGithub}/></a>
                    <a href="#"><FontAwesomeIcon size='2x' className="mx-2" icon={faLinkedin}/></a>
                </div>
                <p>Copyright &#169; Harsh Patel 2021</p>
            </div>
        );
    }
}

export default NavBar;